import { style } from 'dom-helpers';
import React, {useEffect, useState} from 'react';
import {useHistory,useParams} from 'react-router-dom';
import { Nav } from 'react-bootstrap';
import styled from 'styled-components';
import './Detail.scss';
import {CSSTransition} from "react-transition-group";
import {connect} from 'react-redux';


function Detail(props){

  let [alert,alert변경] = useState(true);
  let [누른탭,누른탭변경] = useState(0);
  let [스위치,스위치변경] = useState(false);
    useEffect(()=>{
      let 타이머 = setTimeout(()=>{
        alert변경(false)
      },2000);
      return ()=>clearTimeout(타이머); 
    },[]);


    let {id} = useParams();
    let history = useHistory();
    let 찾은상품 = props.Toast.find((상품)=>{
        return 상품.id == id
    });
    const imgUrl = "/images/toast"+id+".png"


    return(
      <div className="container">
        <h4 className="red">ISAAC MENU</h4>
        {
          alert === true ? <Alert/> : null
        }

      <div className="row">
        <div className="col-md-6">
          <img src={imgUrl}/>
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <button className="btn btn-danger" onClick={()=>{props.dispatch({type:'항목추가', 데이터 :{id:찾은상품.id, name:찾은상품.title, quan:1} });
          history.push('/cart');
          }}>주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{
            history.push('/')
          }}>뒤로가기</button> 
        </div>
      </div>

      {/* <Nav variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>{ 스위치변경(false);누른탭변경(0) }}>Active</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>{ 스위치변경(false); 누른탭변경(1) }}>Option 2</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={()=>{ 스위치변경(false);  누른탭변경(2) }}>Option 2</Nav.Link>
        </Nav.Item>
      </Nav>

      <CSSTransition in={스위치} classNames="wow" timeout={500}>
        <TabContent 누른탭={누른탭} 스위치변경={스위치변경}/>
      </CSSTransition> */}

      
  </div> 
    )
  }

  // function TabContent(props){
  //   useEffect(()=>{
  //     props.스위치변경(true);
  //   });
  //     if(props.누른탭 === 0 ){
  //   return <div>0번쨰 내용입니다</div>
  //   }else if(props.누른탭 === 1){
  //   return <div>1번쨰 내용입니다</div>
  //   }else if(props.누른탭 === 2){
  //   return  <div>2번쨰 내용입니다</div>
  //   }
  // }
  function Alert(){
    return(
      <div className="my-alert">
          <p>주문 즉시 갓 구워낸 토스트에 달콤한 이삭소스</p>
        </div>
    )
  }
  function 주문상품(state){
    return{
        state : state
    }
}
export default connect(주문상품) (Detail);