import React from 'react';
import {Table} from 'react-bootstrap';
import {connect} from 'react-redux';

function Cart(props){
    return(
        <div>
            <Table striped bordered hover>
            <thead>
                <tr>
                <th>번호</th>
                <th>상품명</th>
                <th>개수</th>
                <th>변경</th>
                </tr>
            </thead>
            <tbody>
                {
                    props.state.map((a,i)=>{
                        return(
                            <tr key={i}>
                                <td>{ a.id }</td>
                                <td>{ a.name }</td>
                                <td>{ a.quan }</td>
                                <td><button onClick={()=>{props.dispatch({type:'증가', 데이터: a.id})}}>➕</button>
                                <button onClick={()=>{props.dispatch({type:'감소', 데이터: a.id })}}>➖</button></td>
                            </tr>
                        )
                    })
                }
            </tbody>
            </Table>
        </div>
    )
}
function 주문상품(state){
    return{
        state : state
    }
}
export default connect(주문상품) (Cart)