import React, {useState} from 'react';
import { Navbar,Container,NavDropdown,Nav,Form,FormControl,Button,Carousel,Accordion } from 'react-bootstrap';
import './App.scss';
import Data from './data.js';
import ctData from './ctdata.js';
import bevData from './bevdata.js';
import Detail from './Detail.js';
